/* our process heading */

.ourprocessheading {
    font-size: 1.5rem;
    color: #222;
    font-weight: 500;
      letter-spacing: 21px;
    font-family: sans-serif;
    line-height: 1;
    text-transform: uppercase;
    padding-top: 100px;
    padding-bottom: 100px;
  }
  
  .processtextcenter{
    position: relative;
    z-index: 1;
  }
  .ct{
    text-align: center;
  }
  
  /* background image for carousel */
  .process-images {
    background-size: 207px 348px;
    background-position: center;
    height: 250px;
    width: 100%;
    background-repeat: no-repeat;
  }
  
  /* process section background color */
  .processbackground {
    background-color: white;
  
    position: relative;
    margin-top: 100px;
    margin-bottom: 100px;
  }
  /* clipping the shape of process section */
  /* .processbackground:after {
