  /* owl carousel */
  .owl-nav button {
    position: absolute;
    top: 50%;
    background-color: #000;
    color: #fff;
    margin: 0;
    transition: all 0.3s ease-in-out;
  }
  /* carousel previous and next buttons */
  .owl-nav button.owl-prev {
    left: -20px;
    font-size: 70px !important;
  }
  
  .owl-nav button.owl-next {
    right: -20px;
    font-size: 70px !important;
  }
  /* carousel indicators */
  .owl-dots {
    text-align: center;
    padding-top: 30px;
  }
  
  .owl-dots button.owl-dot {
    width: 15px;
    height: 15px;
    border-radius: 50%;
    display: inline-block;
    background: #ccc;
    margin: 0 3px;
  }
  
  .owl-dots button.owl-dot.active {
    background-color: #000;
  }
  
  .owl-dots button.owl-dot:focus {
    outline: none;
  }
  
  .owl-nav button {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
      background: rgb(255, 255, 255);
    background: rgba(255, 255, 255, 0.38) !important;
  }
  .owl-nav button:focus {
    outline: none;
  }
  /* roman number text on carousel */
  .testimonial {
    margin: 0 20px 40px;
  }
  
  .testimonial .testimonial-content {
    background: #fff;
    position: relative;
  }
  
  .testimonial .testimonial-content:before {
    content: "";
    position: absolute;
    bottom: -30px;
    left: 0;
  }
  
  .testimonial .testimonial-content:after {
    content: "";
    position: absolute;
    bottom: -30px;
    right: 0;
  }
  
  .testimonial-content .testimonial-icon {
    width: 50px;
    height: 45px;
    background: #0CCA4A;
    text-align: center;
    font-size: 22px;
    color: #fff;
    line-height: 42px;
    position: absolute;
    top: 37px;
    left: -19px;
    z-index: 1;
  }
  
  .testimonial-content .testimonial-icon:before {
    content: "";
    border-bottom: 16px solid #05A739;
    border-left: 18px solid transparent;
    position: absolute;
    top: -16px;
    left: 1px;
  }
  button.owl-prev.disabled,button.owl-next.disabled{
    color: #808080 !important;
  }









  /* client requirement */
/* page swinging animation */
#page {
    -webkit-transform-origin: 50% 0;
    -moz-transform-origin: 50% 0;
    -o-transform-origin: 50% 0;
    -ms-transform-origin: 50% 0;
     transform-origin: 50% 0;
     -webkit-animation: swing 3.5s ease-in-out forwards infinite;
     -moz-animation: swing 3.5s ease-in-out forwards infinite;
     -o-animation: swing 3.5s ease-in-out forwards infinite;
     -ms-animation: swing 3.5s ease-in-out forwards infinite;
     animation: swing 3.5s ease-in-out forwards infinite;
  }
  
  @-webkit-keyframes swing {
    0%,100%{transform: rotate(10deg);}
     50%{transform: rotate(-5deg);}
  }
  @-moz-keyframes swing {
    0%,100%{transform: rotate(10deg);}
     50%{transform: rotate(-5deg);}
  }
  @-o-keyframes swing {
    0%,100%{transform: rotate(10deg);}
     50%{transform: rotate(-5deg);}
  }
  @keyframes swing {
    0%,100%{transform: rotate(10deg);}
     50%{transform: rotate(-5deg);}
  }
  
  
  /* design problem */
  /* question mark shaking */
  #questionmark {
    -webkit-animation: shake 0.82s infinite;
    -moz-animation: shake 0.82s infinite;
    -o-animation: shake 0.82s infinite;
    -ms-animation: shake 0.82s infinite;
    animation: shake 0.82s infinite;
    -webkit-transform: translate3d(0, 0, 0);
    -moz-transform: translate3d(0, 0, 0);
    -o-transform: translate3d(0, 0, 0);
    -ms-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
  }
  
  @-webkit-keyframes shake {
    10%, 90% {
      transform: translate3d(-1px, 0, 0);
    }
  
    20%, 80% {
      transform: translate3d(2px, 0, 0);
    }
  
    30%, 50%, 70% {
      transform: translate3d(-4px, 0, 0);
    }
  
    40%, 60% {
      transform: translate3d(4px, 0, 0);
    }
  }
